import React from 'react';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';
import Iframe from 'react-iframe'

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%",
        //background: '#00FF0088', 
    },
    ContentsText: {
        textAlign: 'left'
    },
    contents_site_link: {
        position: 'absolute',
        height: '100%',
        width: '100%',
        frameBorder: 0,
      },
}));

export const PortraitMasterContentsIntro = (props) => {
    console.log('PortraitMasterContentsIntro component start');
    const classes = useStyles();

    return (
        <div className={classes.root}>
              <Iframe url={process.env.PUBLIC_URL + '/contents/index.html'} className={classes.contents_site_link} frameBorder="0"/>;
        </div>
    );
}

const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMasterContentsIntro);