import React from 'react';
import './PortraitMaster.css';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';

import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

import { GetIframeReward } from './util/SendMessage';

import RewardButton from './RewardButton';
import PortraitMasterContentsIntro from './PortraitMasterContentsIntro';
import PortraitMasterContentsFeedback from './PortraitMasterContentsFeedback';
import PortraitMasterSimpleCaptureList from './PortraitMasterSimpleCaptureList'

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  // ContentsBody: {
  //   position: "absolute",
  //   width: "100%",
  //   left: "0px",
  //   top: "0px",
  //   bottom: "50px",
  //   display: "grid",
  //   gridTemplateRows: "50% 50%"
  // },
  BottomBarBack: {
    position: "fixed",
    backgroundColor: '#FFFFFF88',
    width: '100%',
    height: '11%',
    top: '89%',
    left: '0%'
  },
  BottomBarButtonBase: {
    position: "fixed",
    height: "8%",
    top: "90%",
    background: 'linear-gradient(90deg, #cc6699 30%, #6666ff 80%)',
    borderRadius: 3,
    border: 0,
    color: 'white',
    boxShadow: '0 3px 5px 2px rgba(0, 0, 0, .3)',

  },
  BottomBarButtonLeft: {
    width: "44%",
    left: "3%",
  },
  BottomBarButtonRight: {
    width: "44%",
    left: "53%",
  },
  BottomBarButton: {
    width: "94%",
    left: "3%",
  },
  divLine: {
    border: "1px solid",
    color: "#DDDDDD",
    width: "95%"
  }
}));

export const PortraitMaster = ({ _current_scene, set_scene, _canvasData, rewardState, rewardCategory, rewardExp }) => {
  console.log('PortraitMaster component start');
  const classes = useStyles();
  const BottomBarButtonRight = clsx(classes.BottomBarButtonBase, classes.BottomBarButtonRight);
  const BottomBarButtonLeft = clsx(classes.BottomBarButtonBase, classes.BottomBarButtonLeft);
  const BottomBarButton = clsx(classes.BottomBarButtonBase, classes.BottomBarButton);

  const onCameraCapture = () => {
    console.log("onCameraCapture call");
    set_scene("CameraCapture");
  }

  const onCameraCaptureSubmit = () => {
    console.log("onCameraCaptureSubmit call");
    set_scene("SubmitCaptureList");
  }

  const onGetRewardClick = () => {
    console.log("onGetReward call");
    GetIframeReward();
  }

  const GetImageCount = () => {
    if (undefined != window.HybridApp) {
      return window.HybridApp.GetDirectoryFileCount();
    }
    else {
      return _canvasData.length;
    }
  }

  let simpleImageList;
  let bottomButton;
  var imageCount = GetImageCount();
  if (true == rewardState) {
    bottomButton = <RewardButton onClick={onGetRewardClick} rewardCategory={rewardCategory} rewardExp={rewardExp} ></RewardButton>;
  } else {
    if (imageCount == 0) {
      bottomButton = <Button onClick={onCameraCapture} className={BottomBarButton} variant="contained" color="secondary">사진 찍기</Button>;
    } else {
      simpleImageList = <div><PortraitMasterSimpleCaptureList /> <hr className={classes.divLine} /></div>;
      bottomButton = <div>< Button onClick={onCameraCapture} className={BottomBarButtonLeft} variant="contained" color="secondary" > 사진 찍기</Button >
      <Button onClick={onCameraCaptureSubmit} className={BottomBarButtonRight} variant="contained" color="secondary">제출하기</Button></div>;
    }
  }

  const getFeedback = () => {
    if (false == rewardState) {
      return undefined;
    }
    
    return (
      <div>
        <PortraitMasterContentsFeedback />
        <hr className={classes.divLine} />
      </div>
    )
  }

  return (
    <div className="MainSceneRoot">
        {simpleImageList}
        {getFeedback()}
        <PortraitMasterContentsIntro />
      <div className={classes.BottomBarBack}>
        {bottomButton}
      </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
  _current_scene: state.scene._current_scene,
  _canvasData: state.camera._canvasData
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMaster);